body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.page{
  height: 100vh;
  text-align: center;
}

.page-title{
  border-bottom: medium solid black;
  height: 5%;
  margin: 3% 10%;
  padding: 5px;
  font-weight: 2rem;
}

.post{
  height: 100%;
  margin: 3% 10%;
  border-radius: 5%;
  border: medium solid lightgrey;
}

.navbar{
  font-size: 1.5rem;
  color: hsl(0, 0%, 100%);
  height: 5vh;
  width: 100%;
  padding: 0.75% 2%;
}

.navbar-list{
  display: flex;
  justify-content: space-around;
}

.navbar-member{
  color: hsl(0, 0%, 96%);
  text-align: center;
  height: 100%;
}

.navbar-member:focus{
  color: hsl(48, 100%, 67%);
}

.navbar-member:active{
  font-size: 1.3rem;
}
